<template>
    <div id="map"></div>
</template>
<script>
import { mapGetters } from "vuex";
import {getMyTravel} from '../api/travel'
import store from '../store/index'

export default {
    name : "ProfileUserMap",
    watch :{
        '$route' () {
            this.initMap();
        }
    },
    data() {
        return {
            mapContainer: null,
            mapOption: null,
            map: null,
            customOverlay: null,
            nowMarker : null,
            clusterer : null,
            
            };
    },
    mounted() {
        if (window.kakao && window.kakao.maps) {
            this.initMap();
        } else {
            const script = document.createElement("script");

            script.onload = () => kakao.maps.load(this.initMap);
            script.src =
                "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=9361ff07bc1d810e5c855e8be3e33c42";
            document.head.appendChild(script);
        }
    },
    methods: {
      initMap() {
        this.mapContainer = document.getElementById("map"); //   div
        this.mapOption = {
            center: new kakao.maps.LatLng(35.766826, 127.9786567), //  
            level: 13, //   
        };

        this.map = new kakao.maps.Map(this.mapContainer, this.mapOption);
        // this.customOverlay = new kakao.maps.CustomOverlay({});
        // this.infowindow = new kakao.maps.InfoWindow({ removable: true });
        this.addClusterer()

     },
     addClusterer(){
        setTimeout( ()=>{
            if(this.viewUserNickName ){
                
                console.log("ProfileMap.addClusterer")   
                //    
                var imageSrc =
                    "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";
                var imageSize = new kakao.maps.Size(24, 35);
                //   
                var normalImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

                
                //    
                this.clusterer = new kakao.maps.MarkerClusterer({
                    map: this.map, //       
                    averageCenter: true, //          
                    minLevel: 10 //      
                });

                for (let i = 0; i < this.viewUserTravelList.length; i++) {
                    for (let j = 0; j < this.viewUserTravelList[i].pinList.length; j++) {
                        var x = this.viewUserTravelList[i].pinList[j].location.x
                        var y = this.viewUserTravelList[i].pinList[j].location.y  

                        //    
                        var markerPosition  = new kakao.maps.LatLng(y, x);

                        //  
                        var marker = new kakao.maps.Marker({
                            map : this.map,
                            position: markerPosition,
                            title: this.viewUserTravelList[i].pinList[j].location.place_name, //  ,     
                            image: normalImage, //  
                        });

                        var basicInfo = `<div class="customoverlay">
                            <a href="`+ this.viewUserTravelList[i].pinList[j].location.place_url+`" 
                            target="_blank">
                            <span class="title">` 
                                +  this.viewUserTravelList[i].pinList[j].location.place_name +
                            `</span>
                            </a>
                            </div>`;

                        var customOverlay = new kakao.maps.CustomOverlay({
                            clickable: true,
                            content: basicInfo,
                            position: markerPosition,
                            xAnchor: 0.5,
                            yAnchor: 1.9,
                            zIndex: 3,
                        });

                        var moreInfo = `<div class="store-card-item-map">
                            <div
                            class="thumbnail"
                            style="background-image: url('data:image/jpeg;base64,`+ this.viewUserTravelList[i].pinList[j].images[0].base64Image+`');
                                        background-position:center;">
                            </div>
                            <div class="info">
                            <div class="left-side">
                                <div class="first-line">
                                    <div class="title">
                                        `+ this.viewUserTravelList[i].pinList[j].location.place_name +`
                                    </div>
                                    <div class="subtitle">
                                        `+ this.viewUserTravelList[i].pinList[j].location.category_group_name  +`
                                    </div>          
                                </div>
                                <div class="address">
                                    `+ this.viewUserTravelList[i].pinList[j].location.address_name  +`
                                </div>
                            </div>
                            </div>
                        </div>`;

                        var customOverlayMore = new kakao.maps.CustomOverlay({
                            clickable: true,
                            content: moreInfo,
                            position: markerPosition,
                            xAnchor: 0.5,
                            yAnchor: 1.1,
                            zIndex: 3,
                        });


                        kakao.maps.event.addListener(this.map, 'click', this.resetMarker())
                        kakao.maps.event.addListener(marker,'mouseover',this.makeOverListner(this.map,marker,customOverlay))
                        kakao.maps.event.addListener(marker,'mouseout',this.makeOutListner(customOverlay))
                        kakao.maps.event.addListener(marker,'click',this.makeClickListner(this.map,marker,customOverlayMore))
                        this.clusterer.addMarker(marker)
                        console.log(this.clusterer)
                        
                    }
                } 
            }
        },6000) },
        makeOverListner(map, marker, customOverlay){
            return () => {
                if (this.nowMarker) {
                //     
                
                if (marker.getTitle() !== this.nowMarker.name) {
                    //         
                    customOverlay.setMap(map); //  
                }
                } else {
                
                //     
                customOverlay.setMap(map); //  
                // customOverlay.setVisible(true); //   
                }
            }
            
        },
        makeOutListner(customOverlay){
        return () => {

            // customOverlay.setVisible(false);
            customOverlay.setMap(null);
        }

        },
        makeClickListner(map,marker,customOverlayMore){
        return () => {
            if (this.nowMarker) {
            
            this.nowMarker.overlay.setMap(null)
            // this.selectedMarker.overlay.setMap(null); //  ovelay 
            
            }
            const tempMarker = {
            name : marker.Gb,
            overlay : customOverlayMore
            }
            this.nowMarker = tempMarker
            customOverlayMore.setMap(map); //  
            
            map.panTo(marker.getPosition());
            }
        },
        resetMarker(){
            return () => {
                if(this.nowMarker){
                    this.nowMarker.overlay.setMap(null); //  ovelay 
                    if (this.nowMarker) {
                        this.nowMarker = null;
                    }
                }
            }
        },
            
    },
    
    computed: {
        ...mapGetters([
        "viewUserNickName",
        "viewUserTravelList"
        ]),
    }
}
</script>
